@import url(https://fonts.googleapis.com/css?family=Fira+Sans:400,500,600,700,800);
* {
  box-sizing: border-box;
}
body {
  background-image: url(/static/media/veggiestwo.4afcbee7.jpeg) !important;
  background-repeat: round;
  min-height: 100vh;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  font-weight: 400;
  font-family: "Fira Sans", sans-serif;
}

h1,
h2,
h3,
h4,
h5,
h6,
label,
span {
  font-weight: 500;
  font-family: "Fira Sans", sans-serif;
}

body,
html,
.App,
#root,
.auth-wrapper {
  width: 100%;
  height: 100%;
}

.navbar-light {
  background-color: #ffffff;
  box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2);
}

.auth-wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  text-align: left;
  overflow: hidden;
}

.auth-inner {
  margin: auto;
  background: #ffffff;
  box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2);
  padding: 40px 55px 45px 55px;
  border-radius: 15px;
  transition: all 0.3s;
}

.auth-wrapper .form-control:focus {
  border-color: #167bff;
  box-shadow: none;
}

.auth-wrapper h3 {
  text-align: center;
  margin: 0;
  line-height: 1;
  padding-bottom: 20px;
}
.foodList {
  overflow: auto;
}

.custom-control-label {
  font-weight: 400;
}

.forgot-password,
.forgot-password a {
  text-align: right;
  font-size: 13px;
  padding-top: 10px;
  color: #7f7d7d;
  margin: 0;
}

.forgot-password a {
  color: #167bff;
}

.foodCompletionSuggestionButton {
  margin: 5px;
}

.item {
  border: solid 1px;
  border-color: lightgray;
  border-radius: 10px;
  width: 100%;
  margin-bottom: 7px;
  box-shadow: 4px 5px #338e3e58;
}

.deleteButton {
  float: left;
  width: 25px;
  color: red;
  opacity: 50%;
  text-align: center;
  padding-top: 13px;
}
.fridgeButton {
  width: 35px;
  color: green;
  float: right;
}

.foodName {
  text-align: center;
  padding-top: 10px;
  font-weight: 700;
  font-family: Helvetica, sans-serif;
  font-size: x-large;
}

.expDate {
  text-align: center;
  padding-top: 10px;
  font-weight: 700;
  font-family: Helvetica, sans-serif;
  font-size: small;
  padding-left: 10px;
}

#testingOne {
  background-color: purple;
}

.date {
  font-weight: bold;
}

